.navbar {
  display: flex;
  height: 50px;
  border-bottom: 0.5px solid rgb(231,228,228);
  align-items: center;
  // font-size: 14px;
  color: #555;

  .wrapper{
    width: 100%;
    display: flex;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .search {
      display: flex;
      align-items: center;
      border: 0.5px solid lightgray;

      input {
        border: none;
        outline: none;
        background-color: transparent;


        &::placeholder {
          font-size: 18px;
        }
      }
    }
    .items {
      display: flex;
      align-items: center;

      .item{
        display: flex;
        align-items: center;
        margin-right: 20px;
        position: relative;

        .avatar {
          width: 30px;
          height: 30px;
          border-radius: 50%;
        }

        .counter {
          width: 15px;
          height: 15px;
          background-color: red;
          border-radius: 50%;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 10px;
          color: white;
          font-weight: bold;
          position: absolute;
          top: -5px;
          right: -5px;
        }
      }
    }
  }
}